header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

.cvd{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials{
    
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);

}

.me{
    background: linear-gradient(70deg,transparent,rgba(63, 146, 234, 0.388));
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}




/*Medium devices*/
@media screen and (max-width: 1024px){
   


header{
    height: 68vh;
}

}

/*Small devices*/
@media screen and (max-width: 600px){
   
   
    header{
        height: 100vh;
    }
    .header__socials, .scroll__down{
        display: none;
    }
}

